<template>
  <div class="mine">
    <div class="banner">
      <j-header bg="none" title="经纪人"></j-header>
      <div class="icon-box">
        <img :src="userInfo.picture" alt="">
        <p>{{userInfo.username}}</p>
      </div>
      <nav>
        <div>
          <p><span>{{ agentInfo.num }}</span><span>直属客户</span></p>
          <p><span>{{ agentInfo.count_day }}</span><span>今日新增</span></p>
        </div>
        <button @click="dialogShow = !dialogShow">推广</button>
      </nav>
    </div>
    <main>
      <h3>客户列表</h3>
      <p>
        <span>昵称</span>
        <span>手机</span>
        <span>余额</span>
        <span>注册时间</span>
      </p>
      <ul class="list" ref="agent">
        <!-- <li>
          <span>昵称</span>
          <span>手机</span>
          <span>余额</span>
          <span>注册时间</span>
        </li> -->
        <li v-for="obj in agentInfo.data" :key='obj.phone' class="wave">
          <span>{{ obj.username }}</span>
          <span>{{ obj.phone }}</span>
          <span>{{ obj.wallet }}</span>
          <span>{{ obj.createtime.split(' ')[0] }}</span>
        </li>
        <li class="empty" v-show="!agentInfo.data"><span>暂无数据~</span></li>
      </ul>
    </main>
    <j-dialog v-model="dialogShow">
      <qriously :value="agentInfo.qr_url || 'hellow world!'" :size="160"/>
    </j-dialog>
  </div>
</template>

<script>
// @ is an alias to /src
import JHeader from '@/components/JHeader'
import JDialog from '@/components/JDialog'
import { getUid } from '@/utils/auth'
import { scroll } from '@/utils'

export default {
  name: 'agent',
  components: {
    JHeader,
    JDialog
  },
  data () {
    return {
      dialogShow: false,
      userInfo: {},
      agentInfo: {},
      list: [
        {
          username: 'ticket',
          phone: 1889832920,
          balance: '200.00',
          time: '2018/8/7'
        },
        {
          username: 'ticket',
          phone: 1889832920,
          balance: '200.00',
          time: '2018/8/7'
        },
        {
          username: 'ticket',
          phone: 1889832920,
          balance: '200.00',
          time: '2018/8/7'
        }
      ]
    }
  },
  created () {
    this.getUserInfo()
    this.init({ 'X-Pagination-Per-Page': 20 })
  },
  methods: {
    init (obj) {
      this.request({
        url: '/agent',
        method: 'get',
        headers: obj || {},
        loading: true
      }).then(res => {
        if (res.code === 1) this.agentInfo = res.data
      })
    },
    getUserInfo () {
      this.request({
        url: `/user/${getUid()}`,
        method: 'get'
      }).then(res => {
        if (res.code === 1) {
          this.userInfo = res.data
        }
      })
    },
    routerPush (route) {
      this.$router.push({ name: route })
    }
  },
  mounted () {
    const container = this.$refs.agent
    scroll(container, page => {
      this.init({ 'X-Pagination-Per-Page': page * 10 })
    })
  }
}
</script>

<style lang="scss" scoped>
  @import './index.scss';
</style>
